import React from 'react';
import './scss/signOutModal.scss';
import { useDispatch, useSelector } from 'react-redux';
import { signOutModal } from '../../reducer/signOut';
import { messageModal } from '../../reducer/messageModal';
import { signUpModal } from '../../reducer/signUpModal';



export default function MypageDeleteComponent() {

    const dispatch = useDispatch();
    const selector = useSelector((state)=>state);

    const okModalMethod=(msg)=>{
        const obj = {
            isOkModal: true,
            isOkMsg:msg,
        }
        dispatch(signUpModal(obj));
    }

    const onClickcencel=(e)=>{
        e.preventDefault();
        dispatch(signOutModal(false));
    }

    const onClicksignOut=(e)=>{
        e.preventDefault();
        if(localStorage.getItem('PORORO_SIGNIN_DATA')!==null){
            dispatch(signUpModal(true));
            okModalMethod('   ?');
        }
    }

    return (
        <div id='signOut'>
            <div className="container">
                <div className="modal">
                    <div className="title">
                        <h2></h2>
                        <button onClick={onClickcencel}></button>
                    </div>
                    <div className="content">
                        <div className="middle">
                            <ul>
                                <li><span></span><p>   .   : </p><strong> &nbsp;&nbsp;&nbsp;&nbsp;2000</strong></li>
                            </ul>
                        </div>
                        <div className="text">
                            <h3> </h3>
                        </div>
                        <div className="bottom">
                            <span></span>
                            <select name="signout" id="signout">
                                <option value="--" selected>--</option>
                                <option value=" "> </option>
                                <option value=" "> </option>
                                <option value="   ">   </option>
                                <option value=" "> </option>
                                <option value="/ ">/ </option>
                                <option value="   ">   </option>
                                <option value="   (, , )">   (, , )</option>
                                <option value=" "> </option>
                                <option value="  ">  </option>
                                <option value=""></option>
                            </select>
                        </div>
                        <div className="button_box">
                            <button onClick={onClicksignOut} className='signOut_btn'></button>
                            <button onClick={onClickcencel}></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};
