import React from 'react';
import './scss/search.scss';
import { useLocation, useNavigate } from 'react-router-dom';

export default function AdminIdSearchResultComponent () {

    const location = useLocation();
    const navigate = useNavigate();

    const onClickSignIn=()=>{
        navigate('/adminSignIn');
    }
    const onClickPwsearch=()=>{
        navigate('/adminpwsearch');
    }

    return (
        <div id='search'>
            <div className="container">
                <div className="title">
                    <h2>   </h2>
                </div>
                <div className="content">
                    <h3>    .</h3>
                    <div className="result_box">
                    <p>  . <br />   .</p>
                        <div className="user_description">
                            <div className="user_box">
                                <span><img src="./images/sub/search/icon_member.gif" alt="" /></span>
                            </div>
                            <ul className='admin'>
                                <li><span></span><strong>: {location.state.name}</strong></li>
                                <li><span></span><strong>: {location.state.hp}</strong></li>
                                <li><span></span><strong>: {location.state.id}</strong></li>
                                <li><p> !</p></li>
                            </ul>
                        </div>
                    </div>
                    <div className="button_box">
                        <button onClick={onClickSignIn} className='signIn'> </button>
                        <button onClick={onClickPwsearch}>  </button>
                    </div>
                </div>
            </div>
        </div>
    );
};
